.form {
  &__logo {
    margin-bottom: $unit-5;
  }

  &__field,
  &__field-select {
    display: flex;
    flex-direction: column;
    gap: $unit-0;

    &:focus-within {
      .label {
        color: $bbt-primary-green;
      }
    }

    .error-message {
      display: none;
    }

    // Styling for error message
    &.error {
      .form__label {
        color: $bbt-text;
      }

      .form__input {
        color: $bbt-red-100;
        border-bottom-color: $bbt-red-100;

        // Remove background color for autofilled fields
        &:-webkit-autofill {
          -webkit-box-shadow: 0 0 0 1000px $bbt-white inset !important;
          -webkit-text-fill-color: $bbt-red-100 !important;
        }
      }

      .error-message {
        display: block;
        color: $bbt-red-100;
      }
    }
  }

  &__label {
    &.filled {
      color: $bbt-primary-green;
    }
  }

  &__input {
    width: 100%;
    border: none;
    color: $bbt-grey-50;
    border-bottom: toRem(1) solid $bbt-grey-50;
    padding: 0 0 toRem(5);
    background: none;
    outline: none;
    transition: all 0.35s ease-in-out;

    &::placeholder {
      color: $bbt-grey-50;
    }

    &:not(:placeholder-shown) {
      color: $bbt-text;
      border-bottom-color: $bbt-secondary-grey;
    }

    &:hover,
    &:focus,
    &:active {
      color: $bbt-text;
      border-bottom-color: $bbt-secondary-grey;
    }

    // Remove background color for autofilled fields
    &:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 1000px $bbt-white inset !important;
    }
  }

  &__label-mandatory {
    margin-left: toRem(3);
    color: $bbt-orange-100;
  }

  // Select styles
  &__select {
    &.filled {
      .react-select__control {
        border-bottom-color: $bbt-secondary-grey;
      }
    }
  }

  .react-select {
    &__value-container {
      padding: 0;
    }

    &__control {
      min-height: toRem(18);
      border: none;
      border-bottom: toRem(1) solid $bbt-grey-50;
      border-radius: 0;
      background: none;
      box-shadow: none;
      padding: 0;
      transition: border-color 0.35s ease-in-out;
      cursor: pointer;

      &:hover {
        border-bottom-color: $bbt-secondary-grey;

        .react-select__dropdown-indicator {
          background-color: $bbt-primary-green;
        }

        .react-select__placeholder {
          color: $bbt-secondary-grey;
          transition: color .35s ease-in-out;
        }
      }

      &--is-focused {
        border-bottom-color: $bbt-primary-green;
      }
    }

    &__input {
      margin-left: toRem(-2) !important;
    }

    &__placeholder {
      margin-left: 0;
      font-family: $font--boxed-regular;
      font-weight: 500;
      font-size: 14px;
      line-height: 18px;
      color: $bbt-grey-50;
      transition: color .35s ease-in-out;
    }

    &__single-value {
      margin-left: 0;
      color: $bbt-text;
    }

    &__indicator-separator {
      display: none;
    }

    &__indicator {
      color: $bbt-grey-50;
      width: toRem(18);
      height: toRem(18);
      display: inline-block;
      mask-repeat: no-repeat;
      mask-position: center;
      transition: background-color 0.35s;
    }

